import React from 'react'
import { ReactElement } from 'react'

import { SubjectSummary } from '../../../components/subject/SubjectSummary'
import { SubjectContent } from '../../../components/subject/SubjectContent'
import { SubjectTitle } from '../../../components/subject/SubjectTitle'
import { SubjectKeyword } from '../../../components/subject/SubjectKeyword'
import { SubjectIdentifier } from '../../../components/subject/SubjectIdentifier'
import { Subject } from '../../../components/subject/Subject'

export function Motivation () : ReactElement {
  return (
    <Subject>
      <SubjectIdentifier>rules-sanity-motivation</SubjectIdentifier>
      <SubjectKeyword>règle</SubjectKeyword>
      <SubjectTitle>Motivation</SubjectTitle>
      <SubjectSummary>

      </SubjectSummary>
      <SubjectContent>
        <p>
          La motivation est un puissant moteur lorsqu'il s'agit de puiser les
          ressources nécéssaires pour réussir. Un personnage est motivé s'il
          possède un nombre de points de volonté supérieur ou égal aux 3 quarts
          de son score de contrôle. Dans une telle situation, le personnage
          gagne un point bonus à l'ensemble de ses jets quels qu'ils soient.
        </p>

        <p>
          La démotivation est au contraire un frein à toute chose. Un personnage
          est démotivé s'il possède un nombre de points de volonté strictement
          inférieur à la moitiée de son score de contrôle. Dans une telle
          situation, le personnage subit un point de malus à l'ensemble de ses 
          jets quels qu'ils soient.
        </p>
      </SubjectContent>
    </Subject>
  )
}
